import React, { Component } from 'react';
import './App.css'
import Slider from 'react-rangeslider'
class App extends Component {
  constructor(){
    super();
    this.myVar=null;
    this.myTimer = this.myTimer.bind(this);
    this.isConflict = this.isConflict.bind(this);
    this.start = this.start.bind(this);
    this.sliderChanged = this.sliderChanged.bind(this);
    this.message="Lets Play"
this.state={

  user:{name:"shit"},
  authed:false,
  loading: true,
  i:1,
  j:1,
  n:4,
  currRow:1,
  currCol:1,
  cols:[],
  conflictCol: 0,
  start:false,
  running:false,
  speed:100
};
    
  }

  start(){

   let val = document.getElementById("value").value;
   let speed = document.getElementById("speed").value;

  this.message = "Lets Go!!";

   
      this.setState({
        n:val,
        currRow:1,
        currCol:1,
        cols:[],
        conflictCol:0,
        start:true,
        speed:speed
      });
   

  }

  isConflict(col){

    let rows = col.length;

    let prev = col.length -2;
    let now = col.length-1;
    while(prev >= 0 ){
        if(col[prev] === col[now]){
          this.setState({
            conflictCol:col[prev]
          });
          return true;
        }
        prev--;
    }

    

    let left = col.length-2;
    let i=1;
    while(left>=0){
        if(col[left] === col[now]-i){
          this.setState({
            conflictCol:col[left]
          });
          return true;
         
        }
        left --;
        i++;
    }

    let right = col.length-2;
    i=1;
    while(right>=0){
        if(col[right] === col[now]+i){
          this.setState({
            conflictCol:col[right]
          });
          return true;
        }
        right --;
        i++;
    }

        

    return false;
  }
  
  myTimer() {

    //run N-queen
    let currRow = this.state.currRow;
    let currCol  = this.state.currCol;
    let cols = this.state.cols;

    this.setState({
      conflictCol:0
    });

    if(currRow === 0){
      this.message="its Impossible!!!";
      this.setState({
        running:false,
        start:false
      });
      clearInterval(this.myVar);
      return;
    }

    if(currRow > this.state.n){
      clearInterval(this.myVar);
      this.message="We did it!!!";
      this.setState({
        running:false,
        start:false
      });
      
     // console.log(this.state.currCol);
        return;
    }

    if(currCol > this.state.n){

      

      this.setState({
          currCol: cols.pop() + 1,
          currRow:currRow-1,
          cols:cols
      });
      //console.log(this.state.currCol);
      return;
    }

    cols.push(currCol);

    this.setState({
        currCol:currCol+1,
        cols:cols
    });

    if( !this.isConflict(cols)){

      this.setState({
        currRow:currRow+1,
        currCol:1,
        cols:cols
      });
      //console.log("found no conflict");
      //console.log(this.state.currCol);
        return;
    }
   // console.log("cn1 = ", cols);
    cols.pop();
    //console.log("cn2 = ", cols);

    this.setState({
      cols:cols
    });

    
   // console.log(this.state.currCol);


    
  }

  sliderChanged(event){

    let speed = document.getElementById("speed").value;
    clearInterval(this.myVar);
    this.setState({
      running:false,
      speed: speed
    });
  }

 
  
  componentDidUpdate(){
    if(this.state.start && !this.state.running){
      this.myVar = setInterval(this.myTimer, this.state.speed);
      this.setState({
        running:true
      });
    }
  }


  
  render() {

    let squares=[];
    let n=this.state.n;
    

    for(let i=1; i<=n; i++){
      let rows = [];
      for(let j=1; j<=n; j++){
          let shade = "dar";
          if((i%2==0 && j%2==0 )|| (i%2==1 && j%2==1))
            shade = "light";


          if(i===this.state.currRow && j === this.state.currCol -1)
            shade="mar";  
          if( this.state.cols.indexOf(j) === i-1){
            if(this.state.conflictCol === j)
              rows.push(<div className="dash"><span id="demo"><i style={{fontSize: "24px"}} class='fas'>&#xf445;</i></span></div>);
            else
              rows.push(<div className={shade}><span id="demo"><i style={{fontSize: "24px"}} class='fas'>&#xf445;</i></span></div>);  
            }
          else
          rows.push(<div className={shade}></div>);

      }

      squares.push(<div className="row"> {rows}</div> );
      

    }

    

     return(

      <div className="body">
      <nav className="nav_bar">

          <i style={{fontSize: "45px"}} class='fas'>&#xf445;</i>
          <h2 className="texts"> N-Queens-Visualizer</h2>
          <i style={{fontSize: "45px"}} class='fas'>&#xf445;</i>
          

        </nav>
      <div className="App">

        <div className="content-body">

        
        <h2 id="message">{this.message}</h2>
        <div className="main">
          <div className="board">
          {squares}
          </div>
        </div>

        <div class="inputDiv">

          <input id="value" type="text" placeholder="value of n" />
          <button onClick={this.start}>Start</button>
        </div>


        <div class="inputDiv">

          <h4 id="message_2">Speed <sub>ms</sub></h4>
          <input type="range" min="100" max="1000" class="slider" id="speed" onChange={this.sliderChanged}/>
        </div>
       
      </div>
      </div>      
      </div>
        
        );
  }
}

export default App;



